import React,{useState, useEffect} from 'react'
import axios from 'axios'

const HealthBody = () => {
   
    const [tasks, setTasks] = useState([])
    const [selectedTask, setSelectedTask] = useState([])
    const [id, setId] = useState(1)
    const [editedTask, setEditedTask] = useState({id:'',title:''})

    useEffect(()=> {
        axios.get("http://127.0.0.1:8000/api/healthbody/", {
            headers:{
                'Authorization': 'Token 360d3fad4e80a1c1aadc236d2288234e21b0a01c'
            }
        })
        .then(res => {setTasks(res.data)})
    },[])

    // const getTask = () => {
    //     axios.get(`http://127.0.0.1:8000/api/healthbody/${id}/`, {
    //         headers:{
    //             'Authorization': 'Token 360d3fad4e80a1c1aadc236d2288234e21b0a01c'
    //         }})
    //         .then(res => {setSelectedTask(res.data)
    //     })
    // }

    const deleteTask = (id) => {
        axios.delete(`http://127.0.0.1:8000/api/healthbody/${id}/`, {
            headers:{
                'Authorization': 'Token 360d3fad4e80a1c1aadc236d2288234e21b0a01c'
            }})
            .then(res => {setTasks(tasks.filter(task => task.id !== id)); setSelectedTask([])})
    }

    const newTask = (task) => {

        const data = {
            title: task.title
        }
        axios.post("http://127.0.0.1:8000/api/healthbody/", data,{
            headers:{
                'Content-Type' : 'application/json',
                'Authorization': 'Token 360d3fad4e80a1c1aadc236d2288234e21b0a01c'
            }})
            .then(res => {setTasks([...tasks, res.data]); setEditedTask({id:'', title:''})
        })
    }

    const editTask = (task) => {

        axios.put(`http://127.0.0.1:8000/api/healthbody/${task.id}/`, task,{
            headers:{
                'Content-Type' : 'application/json',
                'Authorization': 'Token 360d3fad4e80a1c1aadc236d2288234e21b0a01c'
            }})
            .then(res => {setTasks(tasks.map(task => (task.id === editedTask.id ? res.data : task)));
                setEditedTask({id:'', title:''})
        })
    }

    const handleInputChange = () => evt => {
        const value = evt.target.value;
        const name = evt.target.name;
        setEditedTask({...editedTask, [name]:value})
    }

    return (
        <div>
           

            <h1>健康</h1><br/>
            
            <br/>
            <form className='search-form'>
            {/* <input className="search-bar" type="text" value={id} onChange={evt=>{setId(evt.target.value)}}/>
            <button className="search-button" type='button' onClick={()=>getTask()}>検索</button>
            
            
            <h3>{selectedTask.title}</h3> */}
            <br/>
            <input className="search-bar" type="text" name='title'
            value = {editedTask.title}
            onChange ={handleInputChange()}
            placeholder="add new phrase" required/>
            { editedTask.id? 
            <button className="search-button" onClick={()=>editTask(editedTask)}>編集・更新</button>:
            <button className="search-button" onClick={()=>newTask(editedTask)}>新しく追加する</button> 
             }
             </form>

            <ul>
                {
                    
                   
                    tasks.map(task => <li className="phrases" key={task.id}> {task.title}
                    <button onClick={()=>setEditedTask(task)}>
                    <i className='fas fa-pen'></i>
                   </button>
                    <button onClick={()=>deleteTask(task.id)}>
                        <i className='fas fa-trash-alt'></i>
                    </button>
                   
                    </li> )
                }
            </ul>

        </div>
    )
}


export default HealthBody
